<template>
    <div class="group">
        <div class="bannar_img">
            <img src="../assets/promotion_bg.jpg" alt="cover">
            <span class="downloadApp">打开APP，下载更多</span>
            <button class="download">极速下载</button>
            <button class="open">打开</button>
        </div>
        <div class="login">
            <div class="user_log"><img src="../assets/user_normal.jpg" alt=""></div>
            <span class="login_span">登录发广播</span>
        </div>
        <div class="title">租房找室友</div>
        <div class="tips" v-for="login_broadcast in login_broadcast_list">
            <div class="sec_r"><img :src="login_broadcast.src" alt="">
                <span class="span1">{{login_broadcast.space}}</span>
                <span class="span2">{{login_broadcast.num}}</span>
            </div>
            <div class="time">{{login_broadcast.time}}</div>
        </div>
    </div>
</template>
<script>
export default {
  name: 'group',
  data () {
    return {
      login_broadcast_list: [
        {
          src: 'https://img3.doubanio.com/icon/g117205-2.jpg',
          space: '成都租房',
          num: ' 11354人',
          time: '3分钟前更新：天府四街临江苑套一出租个人 无中介费噢'
        },
        {
          src: 'https://img3.doubanio.com/icon/g279962-3.jpg',
          space: '北京租房',
          num: ' 12132人',
          time: '3分钟前更新：天府四街临江苑套一出租个人 无中介费噢'
        },
        {
          src: 'https://img3.doubanio.com/icon/g117205-2.jpg',
          space: '上海租房',
          num: ' 13354人',
          time: '3分钟前更新：天府四街临江苑套一出租个人 无中介费噢'
        },
        {
          src: 'https://img3.doubanio.com/icon/g117205-2.jpg',
          space: '深圳租房',
          num: ' 12321人',
          time: '3分钟前更新：天府四街临江苑套一出租个人 无中介费噢'
        }
      ]
    }
  }
}
</script>

<style>
.group {
  width: 100%;
  margin-top: 65px;
}
.bannar_img {
  width: 100%;
  height: 75px;
  margin: 58px 0 0 0;
  padding: 0;
  position: relative;
}
.bannar_img img {
  width: 100%;
  height: 100%;
}
.downloadApp {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 0.8rem;
  font-weight: 400;
  line-height: 2.2rem;
  color: #2ca532;
}
.download {
  position: absolute;
  top: 25px;
  right: 5rem;
  width: auto;
  padding: 0 0.6rem;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 2;
  border: 0.1rem solid #42bd56;
  border-radius: 0.3rem;
  color: #fff;
  background: #42bd56;
  outline: none;
}
.open {
  position: absolute;
  top: 25px;
  right: 20px;
  width: auto;
  padding: 0 0.6rem;
  font-size: 0.75rem;
  font-weight: 700;
  line-height: 2;
  border: 0.1rem solid #42bd56;
  border-radius: 0.3rem;
  color: #42bd56;
  background: #fff;
  outline: none;
}
.login {
  position: relative;
  padding: 0.5rem 1rem;
  overflow: hidden;
  border-bottom: 2px solid #e8e8e8;
  display: flex;
  flex-flow: row;
}
.user_log img {
  border-radius: 50%;
}
.login_span {
  padding-left: 10px;
  line-height: 51px;
  color: #888;
  font-size: 0.9rem;
}
.tips {
  width: 300px;
  display: flex;
  flex-flow: column;
  margin: 20px auto;
  border-bottom: 1px solid #eee;
}
.sec_r {
  width: 300px;
  height: 52px;
  line-height: 52px;
  display: flex;
  flex-flow: row;
}
.sec_r img {
  width: 48px;
  height: 48px;
}
.span1 {
  height: 52px;
  line-height: 52px;
  margin-left: 10px;
  color: #111;
  font-size: 1.2rem;
}
.span2 {
  margin-left: 100px;
}
.time {
  margin: 5px 0 15px 0;
  font-size: 0.9rem;
  color: #aaa;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.title {
  font-size: 1.1rem;
}
</style>



