<template>
 <div class="home_view">
     <ul class="home_menu">
        <li>
          <router-link :to="{name: 'MovieView'}">影院热映</router-link>
        </li>
        <li>
          <router-link :to="{name: 'BookView'}">好书推荐</router-link>
        </li>
        <li>
          <router-link :to="{name: 'MovieView'}">注册帐号</router-link>
        </li>
        <li>
          <router-link :to="{name: 'BookView'}">登录豆瓣</router-link>
        </li>
     </ul>
     <Loading  v-show="showLoading"></Loading> 
     <ul class="home_loading_list">
         <li v-for="home_loading_list in home_loading_lists">
             <router-link  :to="{name: 'DetailView', params: { id: home_loading_list.id }}">
             <div class="left">
                 <p class="title">{{home_loading_list.title}}</p>
                 <p class="content">{{home_loading_list.content}}</p>
                 <p class="type">{{home_loading_list.subcategory_name}}</p>
            </div>
             <div class="right">
                 <img :src="home_loading_list.image_lmobile" class="img">
                 <p class="from">本活动来自:{{home_loading_list.category_name}}</p>
             </div>
             </router-link> 
         </li>
     </ul>  
 </div>
</template>
<script>
import Loading from '../components/Loading'
export default {
  name: 'home',
  components: {Loading},
  mounted: function () {
    this.loadlist()
  },
  data () {
    return {
      home_loading_lists: [],
      showLoading: true
    }
  },
  methods: {
    loadlist: function () {
      // GET /someUrl
      this.$http.jsonp('https://api.douban.com/v2/event/list?loc=108288&count=10').then(
        response => {
          // get body data
          this.showLoading = false
          this.home_loading_lists = response.data.events
          console.log(response.data)
        },
        response => {
          // error callback
        }
      )
    }
  }

}
</script>
<style>
.home_view {
  margin-top: 80px;
}
.home_menu {
  width: 90%;
  margin: 10px 5% 25px 5%;
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
}
.home_menu li {
  width: 48%;
  height: 24px;
  margin: 3px 1%;
  text-align: center;
  line-height: 24px;
  padding: 15px 0;
  background: #f6f6f6;
  color: #111;
  border-radius: 4px;
}
.home_loading_list {
  width: 90%;
  margin: 10px 5%;
}
.home_loading_list li {
    margin: 15px 0;
    border-bottom: 1px solid #ccc;
}
.home_loading_list li a{
  display: block;
  width: 100%;
  height: 131px;
  margin: 10px 0;
  display: flex;
  justify-content:space-between;
  
}
.home_loading_list li .left {
  width: 70%;
  height: 100%;
}
.home_loading_list li .left .title{
    line-height: 1.41;
    text-align: justify;
    font-size: 1rem;
    font-weight: 500;
    color: #494949;
    margin: 0;
}
.home_loading_list li .left .content{
 width: 100%;
 height: 40px;
 line-height: 20px;
 font-size: .75rem;
 overflow: hidden;
 color: #aaa;
}
.home_loading_list li .left .type{
 width: 100%;
 height: 20px;
 line-height: 20px;
 font-size: .65rem;
 color: #ccc;
}
.home_loading_list li .right {
  width: 30%;
  height: 100%;
}
.home_loading_list li .right .img{
  width: 80%;
  height:104px;
  margin-left: 20%;
}
.home_loading_list li .right .from{
  width: 100%;
  color: #ccc;
  font-size: 0.75rem;

}
</style>





