<template>
  <div class="subject">
    <Loading v-show="showLoading"></Loading>
    <div class="bannar_img" v-show="showBanner">
      <img src="../assets/promotion_bg.jpg" alt="cover">
      <span class="downloadApp">聊聊你的观影感受</span>
      <button class="download">极速下载</button>
      <button class="open">打开</button>
    </div>
    <div class="movie_detail">
      <h1 class="movie_title">{{detail_list.title}}</h1>
      <div class="movie_info_wrapper">
        <div class="movie_info_wrods">
          <div class="movie_rang">
            <div class="rang_img"><img ref="rang_img" src="../assets/ic_rating_s.png" /></div>
            <span class="rang_g">{{rang}}</span>
            <span class="rang_p">{{detail_list.ratings_count}}人评价</span>
          </div>
          <div class="movie_info_tips">{{detail}}</div>
        </div>
        <div class="movie_info_pic"><img :src="movie_img"></div>
      </div>
      <div class="movie_button">
        <div class="want">想看</div>
        <div class="had">看过</div>
      </div>
      <div class="app_tips">在App上保存你的观影足迹</div>
      <div class="movie_intrc">
        <p class="movie_summary_title">{{detail_list.title}}的剧情介绍</p>
        <p class="movie_summary">{{detail_list.summary}}</p>
      </div>
    </div>
  </div>
</template>
<script>
  import Loading from '../components/Loading'
export default {
    name: 'subject',
    components: {
      Loading
    },
    mounted: function () {},
    data () {
      return {
        showLoading: true,
        showBanner: false,
        detail_list: [],
        detail: '',
        rang: ' ',
        movie_img: ''
      }
  },
    created () {
      // Getting route params
      const id = this.$route.params.id
      console.log(id)
      this.$http.jsonp('https://api.douban.com/v2/book/' + id).then(
        response => {
          // get body data
          this.showLoading = false
          this.showBanner = true
          this.detail_list = response.data
          this.rang = response.data.rating.average
          this.detail = response.data.author[0] + '/' + response.data.binding + '/' + response.data.publisher + '/' + response.data.price + '/' + response.data.pages + '页' + '/' + response.data.translator + '（翻译）'
          this.movie_img = response.data.images.medium
          if (response.data.rating.average == 0) {} else if (response.data.rating.average > 9 && response.data.rating.average <= 10) {
            this.$refs.rang_img.style.top = '0px'
          } else if (
            response.data.rating.average > 8 &&
            response.data.rating.average <= 9
          ) {
            this.$refs.rang_img.style.marginTop = '-12px'
          } else if (
            response.data.rating.average > 7 &&
            response.data.rating.average <= 8
          ) {
            this.$refs.rang_img.style.marginTop = '-24px'
          } else if (
            response.data.rating.average > 6 &&
            response.data.rating.average <= 7
          ) {
          this.$refs.rang_img.style.marginTop = '-36px'
        } else if (
            response.data.rating.average > 5 &&
            response.data.rating.average <= 6
          ) {
            this.$refs.rang_img.style.marginTop = '-48px'
          } else if (
            response.data.rating.average > 4 &&
            response.data.rating.average <= 5
          ) {
          this.$refs.rang_img.style.marginTop = '-60px'
        } else if (
            response.data.rating.average > 3 &&
            response.data.rating.average <= 4
          ) {
          this.$refs.rang_img.style.marginTop = '-72px'
        } else if (
            response.data.rating.average > 2 &&
            response.data.rating.average <= 3
          ) {
          this.$refs.rang_img.style.marginTop = '-84px'
        } else if (
            response.data.rating.average > 1 &&
            response.data.rating.average <= 2
          ) {
          this.$refs.rang_img.style.marginTop = '-96px'
        } else if (
            response.data.rating.average > 0 &&
            response.data.rating.average <= 1
          ) {
          this.$refs.rang_img.style.marginTop = '-108px'
        }
        },
        response => {
          // error callback
        }
      )
  }
  }
</script>
<style>
  .subject {
    margin-top: 75px;
  }
  .bannar_img {
    width: 100%;
    height: 75px;
    margin: 58px 0 0 0;
    padding: 0;
    position: relative;
  }
  .bannar_img img {
    width: 100%;
    height: 100%;
  }
  .downloadApp {
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 0.8rem;
    font-weight: 400;
    line-height: 2.2rem;
    color: #2ca532;
  }
  .download {
    position: absolute;
    top: 25px;
    right: 5rem;
    width: auto;
    padding: 0 0.6rem;
    font-size: 0.75rem;
    font-weight: 700;
    line-height: 2;
    border: 0.1rem solid #42bd56;
    border-radius: 0.3rem;
    color: #fff;
    background: #42bd56;
    outline: none;
  }
  .open {
    position: absolute;
    top: 25px;
    right: 20px;
    width: auto;
    padding: 0 0.6rem;
    font-size: 0.75rem;
    font-weight: 700;
    line-height: 2;
    border: 0.1rem solid #42bd56;
    border-radius: 0.3rem;
    color: #42bd56;
    background: #fff;
    outline: none;
  }
  .movie_detail {
    width: 90%;
    margin: 5%;
  }
  .movie_title {
    display: block;
    font-size: 1.2em;
    -webkit-margin-before: 0.67em;
    -webkit-margin-after: 0.67em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    font-weight: bold;
  }
  .movie_info_wrapper {
    width: 100%;
    display: flex;
    flex-flow: row;
  }
  .movie_info_wrods {
    width: 60%;
    margin-right: 5%;
    display: flex;
    flex-flow: column;
  }
  .movie_info_pic {
    width: 35%;
  }
  .movie_info_pic img {
    width: 100%;
    height: 100%;
  }
  .movie_rang {
    width: 100%;
    display: flex;
    flex-flow: row;
  }
  .rang_img {
    display: block;
    width: 60px;
    height: 11px;
    background-position: 0 0;
    margin: 5px 5px 5px 0;
    overflow: hidden;
  }
  .rang_img img {
    width: 100%;
  }
  .rang_g {
    margin-right: 10px;
  }
  .rang_p {
    color: #888;
    font-size: 0.8rem;
    line-height: 22px;
  }
  .movie_info_tips {
    margin-top: 15px;
    font-size: 0.95rem;
    padding: 3px 0;
  }
  .movie_button {
    width: 100%;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
  }
  .movie_button div {
    width: 48%;
    height: 30px;
    border: 1px solid orange;
    border-radius: 4px;
    text-align: center;
    line-height: 30px;
    color: orange;
    font-size: 0.9rem;
  }
  .app_tips {
    width: 100%;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 20px;
    border: 1px solid green;
    border-radius: 4px;
    text-align: center;
    line-height: 30px;
    color: green;
    font-size: 0.9rem;
  }
  .movie_summary_title {
    color: #888;
    font-size: 0.85rem;
    padding: 20px 0 10px 0;
  }
</style>




