import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import PagesView from '../views/PagesView'
import MovieView from '../views/MovieView'
import BookView from '../views/BookView'
import Broadcast from '../views/Broadcast'
import Group from '../views/Group'
import HomeView from '../views/HomeView'
import DetailView from '../views/DetailView'
import SubjectView from '../views/SubjectView'
import SubjectBook from '../views/Subject'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/pages/home'
    },
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/pages',
      component: PagesView,
      children: [
        {
          path: 'home',
          name: 'HomeView',
          component: HomeView
        },
        {
          path: 'movie',
          name: 'MovieView',
          component: MovieView
        },
        {
          path: 'book',
          name: 'BookView',
          component: BookView
        },
        {
          path: 'broadcast',
          name: 'Broadcast',
          component: Broadcast
        },
        {
          path: 'group',
          name: 'Group',
          component: Group
        },
        {
          path: 'detail/:id',
          name: 'DetailView',
          component: DetailView
        },
        {
          path: '/pages/subject/:id',
          name: 'SubjectView',
          component: SubjectView
        },
        {
          path: '/pages/book/:id',
          name: 'SubjectBook',
          component: SubjectBook
        }
      ]
    }

  ]
})



